    .k-side{
      position: absolute;
    
      left: 47%;
      height:100%;
      width: 50%;
      background-color: #3333331c;
      overflow: scroll;
      border-radius: 10px;
    }
    .k-side::-webkit-scrollbar {
      width: 5px;
      height:0px;
       display: block;
      border-radius: 20px;
      cursor: pointer;
    }
    
    .k-side::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
      border-radius: 10px;
      cursor: pointer;
    }
    
    .k-side::-webkit-scrollbar-thumb {
      background: #5ca885; 
      border-radius: 8px;
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
