body{
  /* background-color: rgb(29, 29, 29); */
  background-color: #242323 ;

  
}
 /* width */
 ::-webkit-scrollbar {
  width: 10px;
  height:5px;
}

/* Track */
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #000000; 
  border-radius: 2px;
}
 
/* Handle */
body::-webkit-scrollbar-thumb {
  background: #5ca885; 
  border-radius: 1px;
}

/* Handle on hover */
body::-webkit-scrollbar-thumb:hover {
  background:#28523e; 
  
}

.Pop{
  -webkit-animation: animatezoom 0.6s;
  animation: animatezoom 0.6s;
  position: fixed;
  left: 74%;
  top: 75%;
  width: 25%;
  height: 13%;
  border-radius: 20px;
  box-shadow: 0 2px 10px -3px #5ca885;
  transition: 2x.3s ease 0s;
  background-color: #141414;
}
.top{
  position: fixed;
  top:0px;
  left: 250px;
  background-color: #161616;
  width: 82%;
  height: 10%;
  z-index: 3;
}
#dip{
  position: absolute;
  left: 15px;
  cursor: pointer;
    width: 25px;
    color: white;
    height: 30px;
    border: none;
    text-decoration: none;
    padding: 10px;
    background-color: rgba(48, 47, 47, 0.521);
    align-items: center;
    border-radius: 7px;
    display: none;

}
#dip2{
  position: absolute;
  top: 30px;
  left: 30px;
  color: white;
}

@keyframes animatezoom {
  from {transform: scale(0)} 
  to {transform: scale(1)}
}
.test{
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 30px;
  min-height: 30rem;
}

