.flame{
    height: 5px;
    width: 5px;
    background-color: #ffffff;
    border-radius: 30px 0px 30px 30px;
    transform: rotate(-45deg);
    box-shadow: 0px 0px 2px 10px #ff8000,0px 0px 0px 10px rgb(19, 0, 87);
    display: inline-block;
    animation: glow 2s infinite;
  }

  @keyframes glow{
    0%{ box-shadow: 0px 0px 2px 5px #fc0000,0px 0px 0px 10px rgb(255, 0, 0);}
    50%{ box-shadow: 0px 0px 2px 5px #300101,0px 0px 0px 10px rgb(255, 0, 0);}
    100%{ box-shadow: 0px 0px 2px 5px #4e0000,0px 0px 0px 10px rgb(255, 0, 0);}
  } 

  .likebtn{ 
    cursor: pointer;
    width: 60px;
    height: 30px;
    border-radius: 20px;
    
    background-color: #5ca88500;
    fill:#000000;
    border: none;
  }
#blame{
    width: 39px;
    height: 30px;
  
}
  .lame{
    height: 5px;
    width: 5px;
    background-color: #ffffff;
    border-radius: 30px 0px 30px 30px;
    transform: rotate(-45deg);
    box-shadow: 0px 0px 2px 10px #ff8000,0px 0px 0px 10px rgb(19, 0, 87);
    display: inline-block;
    animation: glowe 2s infinite;
  }
  @keyframes glowe{
    0%{ box-shadow: 0px 0px 2px 5px #fc0000,0px 0px 0px 10px rgb(255, 255, 255);}
    50%{ box-shadow: 0px 0px 2px 5px #300101,0px 0px 0px 10px rgb(190, 206, 255);}
    100%{ box-shadow: 0px 0px 2px 5px #4e0000,0px 0px 0px 10px rgb(210, 217, 253);}
  } 
 
  #blame:hover .lame{
    animation: glow 2s infinite;

  }
  #blame:active.lame{
    animation: glowee 2s infinite;
      
  }
  @keyframes glowee{
    0%{ box-shadow: 0px 0px 2px 5px #fc000000,0px 0px 0px 10px rgb(255, 255, 255);}
    50%{ box-shadow: 0px 0px 2px 5px #30010100,0px 0px 0px 10px rgb(190, 206, 255);}
    100%{ box-shadow: 0px 0px 2px 5px #4e000000,0px 0px 0px 10px rgb(210, 217, 253);}
  } 