#change12::-webkit-file-upload-button {
  visibility: hidden;
}
#change12::before {
  content: 'Select a Profile Picture'; 
  -webkit-user-select: none;
  position: absolute;
  top: 51%;
  left: 30%;
  border-radius: 100px;
  border: none;
  background-color: #00000009;
  width:auto;
  padding: 10px;
  color: #FFFFFF;
  margin-top: 25px;
  box-shadow: 0 2px 10px -3px #38383800;
  display: block;
  margin: 55px auto 10px auto;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-weight: 650;
  cursor: pointer;

}
#change12:hover::before {
  border-color: black;
}
#change12:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}

