.p-img {
 position: absolute;
 top: 15%;
 width: 600px;
}
.p-imgs {
  
  width: 120px;
  height: 120px;
  border-radius: 20px;
 
  color: black;
  padding: 1rem;
  cursor: pointer;
  
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
.p-imgs:hover{
  background-color: rgba(0, 0, 0, 0.103);
  transform: translatey(-1px);
  opacity: 1.2;

  
}
@media (min-width: 600px) {
  .p-img { grid-template-columns: repeat(2, 1fr); }
}

@media (min-width: 900px) {
  .p-img { grid-template-columns: repeat(3, 1fr); }
}









